.projects__main-grid{
	display:flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	row-gap: 1rem;
	column-gap: 1rem;
	margin-top: 1rem;
}

.ItemCard__main {
	width: 18em;
	height: 18rem;
	border-radius: 10px;
	margin-right: 0rem;
	text-align: center;
}
.ItemCard__main-content {
	text-align: center;
}
footer {
	display: block;
}

.ItemCard__main-content-list {
	padding: 0px;
	margin-top: 30px;
	display: flex;
	flex-direction: row;
}
.ItemCard__main-content-text {
	height: 100px;
}
.MuiPaper-elevation {
	background-color: rgba(230, 230, 255, 0.5);
	max-width: max-content;
	margin: 2px;
	padding-left: 2px;
	padding-right: 2px;
	text-shadow: 10px;
}
.MuiPaper-elevation:hover {
	transform: translateY(-1%);
	box-shadow: 0px 0px 2px rgba(255, 105, 180, 0.8);
}
.techlist{
	padding-left:.1em;
	padding-right:.1em;
}
.techWrapper{
	display:flex;
	flex-direction: row;
	width:auto;
	flex-wrap: nowrap;
	justify-content: center;
}